.analysis-table-container {
  background: white;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  overflow: hidden;
  margin: 2rem 0;
}

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: #f8f9fa;
  border-bottom: 1px solid #dee2e6;
}

.table-header h3 {
  margin: 0;
  color: #2c3e50;
}

.export-btn {
  background: #667eea;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s;
}

.export-btn:hover {
  background: #5a6fd8;
}

.table-wrapper {
  overflow-x: auto;
}

.analysis-table {
  width: 100%;
  border-collapse: collapse;
}

.analysis-table thead th {
  background: #2c3e50;
  color: white;
  padding: 1rem;
  text-align: left;
  font-weight: 600;
  position: relative;
}

.analysis-table thead th.sortable {
  cursor: pointer;
  user-select: none;
}

.analysis-table thead th.sortable:hover {
  background: #34495e;
}

.analysis-table tbody tr {
  border-bottom: 1px solid #dee2e6;
  transition: background-color 0.2s;
}

.analysis-table tbody tr:hover:not(.expanded-content) {
  background: #f8f9fa;
}

.analysis-table tbody tr.expanded {
  background: #e3f2fd;
}

.analysis-table td {
  padding: 1rem;
  vertical-align: top;
}

.character-cell {
  font-weight: 600;
  color: #667eea;
  min-width: 120px;
}

.cue-cell {
  min-width: 200px;
}

.physical-cue {
  background: #fff3cd;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-style: italic;
  border-left: 3px solid #ffc107;
}

.chapter-cell {
  color: #6c757d;
  font-size: 0.9rem;
  min-width: 100px;
}

.details-cell {
  width: 60px;
  text-align: center;
}

.expand-btn {
  background: none;
  border: 2px solid #667eea;
  color: #667eea;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.expand-btn:hover {
  background: #667eea;
  color: white;
}

.expanded-content td {
  background: #f8f9fa;
  border-top: none;
}

.expanded-details {
  padding: 1rem;
  max-width: 100%;
}

.passage-section {
  margin-bottom: 1.5rem;
}

.passage-section blockquote {
  margin: 0.5rem 0;
  padding: 1rem;
  background: white;
  border-left: 4px solid #667eea;
  border-radius: 4px;
  font-style: italic;
  color: #444;
}

.meaning-section strong {
  color: #2c3e50;
  display: block;
  margin-bottom: 0.5rem;
}

.meaning-section p {
  color: #555;
  line-height: 1.6;
  margin: 0;
}

.table-footer {
  padding: 1rem 1.5rem;
  background: #f8f9fa;
  border-top: 1px solid #dee2e6;
}

.table-info {
  margin: 0;
  color: #6c757d;
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .table-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .export-btn {
    align-self: flex-end;
  }
  
  .analysis-table {
    font-size: 0.9rem;
  }
  
  .analysis-table td {
    padding: 0.75rem 0.5rem;
  }
  
  .expanded-details {
    padding: 0.5rem;
  }
}